import { FC, SetStateAction, useState, useEffect } from "react";
// import i18n from '../locales/i18n';
import { MenuItem, Select } from "@mui/material";
import IRFlag from "../images/icons/ic_flag_ir.png";
import ENFlag from "../images/icons/ic_flag_en.png";
import TRFlag from "../images/icons/ic_flag_tr.png";
import SAFlag from "../images/icons/ic_flag_sa.png";
import { setScreenLoading } from "../utils";

function getCookie(name: any) {
	const value = `; ${document.cookie}`;
	const parts = value.split(`; ${name}=`) as Array<string>;
	if (parts.length === 2) return parts.pop()?.split(";").shift();
	return "";
}

const LanguageSelector: FC<{ className?: string }> = (props) => {
	const [siteLanguage, setSiteLanguage] = useState(
		localStorage.getItem("i18nextLng") || "fa"
	);
	const handeSiteLanguageChange = (event: {
		target: { value: SetStateAction<string> };
	}) => {
		// setSiteLanguage(event.target.value)
		// i18n.changeLanguage(event.target.value as string)
		const lang = (event.target.value as string) || "fa";
		localStorage.setItem("i18nextLng", lang);
		document.cookie = `ClickMIS-Language=${lang};max-age=6048000;`;
		setScreenLoading(true);
		window.location.reload();
	};

	useEffect(() => {
		document.cookie = `ClickMIS-Language=${
			getCookie("ClickMIS-Language") || "fa"
		};max-age=6048000;`;

		return () => {};
	}, []);

	return (
		<Select
			labelId="siteLanguage"
			id="siteLanguage"
			value={siteLanguage}
			onChange={handeSiteLanguageChange}
			autoWidth
			sx={{
				width: 200,
			}}
			MenuProps={{
				PaperProps: {
					style: {
						width: 200,
					},
				},
			}}
			className={props.className || ""}
		>
			<MenuItem value={"fa"} classes={{ root: "mx-2 rounded-lg px-2 py-3" }}>
				<div className="flex items-center gap-2">
					<img src={IRFlag} alt="IRFlag" className="h-5 w-7" />
					<span>فارسی</span>
				</div>
			</MenuItem>
			<MenuItem value={"en"} classes={{ root: "mx-2 rounded-lg px-2 py-3" }}>
				<div className="flex items-center gap-2">
					<img src={ENFlag} alt="ENFlag" className="h-5 w-7" />
					<span>English</span>
				</div>
			</MenuItem>
			<MenuItem value={"tr"} classes={{ root: "mx-2 rounded-lg px-2 py-3" }}>
				<div className="flex items-center gap-2">
					<img src={TRFlag} alt="TRFlag" className="h-5 w-7" />
					<span>Türkçe</span>
				</div>
			</MenuItem>
			<MenuItem value={"ar"} classes={{ root: "mx-2 rounded-lg px-2 py-3" }}>
				<div className="flex items-center gap-2">
					<img src={SAFlag} alt="SAFlag" className="h-5 w-7" />
					<span>العربیة</span>
				</div>
			</MenuItem>
		</Select>
	);
};
export default LanguageSelector;
