import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

import faLocales from "./fa.json";
import enLocales from "./en.json";
import saLocales from "./sa.json";
import task from "./task/fa.json";
import trLocales from "./tr.json";
import helpFA from "./help/fa.json";
import homeFA from "./home/fa.json";
import featuresFa from "./features/fa.json";
import plansFa from "./plans/fa.json";
import blogFa from "./blog/fa.json";
import faqFa from "./faq/fa.json";

i18n
	.use(LanguageDetector)
	.use(initReactI18next) // passes i18n down to react-i18next
	.init({
		// the translations
		// (tip move them in a JSON file and import them,
		// or even better, manage them via a UI: https://react.i18next.com/guides/multiple-translation-files#manage-your-translations-with-a-management-gui)
		resources: {
			fa: { translations: faLocales },
			en: { translations: enLocales },
			ar: { translations: saLocales },
			tr: { translations: trLocales },
		},
		lng: localStorage.getItem("i18nextLng") || "fa", // if you're using a language detector, do not define the lng option
		fallbackLng: "en",
		debug: false,
		ns: ["translations"],
		defaultNS: "translations",
		interpolation: {
			escapeValue: false, // react already safes from xss => https://www.i18next.com/translation-function/interpolation#unescape
		},
	});

const i18next = i18n.cloneInstance();

i18next.addResourceBundle("fa", "task", task);
i18next.addResourceBundle("fa", "help", helpFA);
i18next.addResourceBundle("fa", "home", homeFA);
i18next.addResourceBundle("fa", "features", featuresFa);
i18next.addResourceBundle("fa", "plans", plansFa);
i18next.addResourceBundle("fa", "blog", blogFa);
i18next.addResourceBundle("fa", "faq", faqFa);

export default i18n;
