	// apiUrl: "https://demo.clickmis.net/api",
	// apiUrl: "https://clickmis.net/api",
	// apiUrl: "http://localhost:5219/api",
	demoUrl: "https://demo.clickmis.net",
	siteUrl: "https://{domain}.clickmis.net",
	home: "https://clickmis.net",
