import {
	BrowserRouter as Router,
	Routes,
	Route,
	Navigate,
	Outlet,
} from "react-router-dom";
import "./locales/i18n";

import Login from "./pages/auth/Login";

import React from "react";
import axiosInstance from "./utils/axios-instance";
import { useSnackbar } from "notistack";

import setting from "./settings";
import { setServerDate } from "./utils/utils";
import Home from "./pages/Home";

import ScreenLoading from "./components/ScreenLoading";

import FaqForm from "./pages/FaqForm";
import FAQ from "./pages/Faq";
import ProtectedRoute from "./components/ProtectedRoutes";
import Home2 from "./components/Home2";

//init axios
let _reqInterceptor = -1;
let _resInterceptor = -1;

function lazyLoadRoutes(componentName: string) {
	//@ts-ignore
	const LazyElement = React.lazy(() => import(`./Routes`));
	// Wrapping around the suspense component is mandatory
	return (
		<React.Suspense fallback={<ScreenLoading />}>
			<LazyElement routeName={componentName} />
		</React.Suspense>
	);
}

const App = () => {
	// const navigate = useNavigate();
	const { enqueueSnackbar } = useSnackbar();
	const [settingLoading, setSettingLoading] = React.useState(true);

	const initAxios = () => {
		console.log("useEffect app");
		//_reqInterceptor -> -1 if not inited
		if (_reqInterceptor !== -1) return;
		_reqInterceptor = axiosInstance.interceptors.request.use(
			(config) => {
				config.headers.Authorization = `Bearer ${
					window.localStorage.token || ""
				}`;

				config.metadata = { startTime: new Date() };

				return config;
			},
			(error) => {
				return Promise.reject(error);
			}
		);

		_resInterceptor = axiosInstance.interceptors.response.use(
			function (response) {
				// Any status code that lie within the range of 2xx cause this function to trigger
				// Do something with response data
				response.config.metadata.endTime = new Date();
				response.duration =
					response.config.metadata.endTime - response.config.metadata.startTime;

				setServerDate(
					new Date(
						+new Date(response.headers.date || new Date()) +
							response.duration +
							1000
					)
				);
				return response;
			},
			function (error) {
				// Any status codes that falls outside the range of 2xx cause this function to trigger
				// Do something with response error

				if (error.response?.status === 401) {
					//need login

					const msg = window.localStorage.token
						? "دوباره با حساب‌کاربری خود وارد شوید"
						: "ابتدا باید با حساب‌کاربری خود لاگین نمایید";
					enqueueSnackbar(msg, { variant: "error" });
					//no need show message
					error._handled = true;
					window.location.replace("/login");
				}

				if (
					!window.navigator.onLine &&
					!error.response &&
					error.code === "ERR_NETWORK"
				) {
					//internet error
					enqueueSnackbar(
						"عدم دسترسی به اینترنت. اتصال اینترنت خود را بررسی نمایید.",
						{ variant: "error" }
					);

					//no need show message
					error._handled = true;
				} else if (error.code === "ERR_NETWORK") {
					//other errors
					enqueueSnackbar(
						"عدم دسترسی به سرور کلیک. اتصال اینترنت خود (وی پی ان و ...) را بررسی نمایید.",
						{ variant: "error" }
					);

					//no need show message
					error._handled = true;
				}

				return Promise.reject(error);
			}
		);
	};

	initAxios();

	React.useEffect(() => {
		const lang = localStorage.getItem("i18nextLng") || "fa";
		document.documentElement.lang = lang;
		document.documentElement.dir = ["fa", "sa", "ar"].includes(lang)
			? "rtl"
			: "ltr";
		return () => {
			//eject interceptor
			axiosInstance.interceptors.request.eject(_reqInterceptor);
			axiosInstance.interceptors.response.eject(_resInterceptor);
			_reqInterceptor = -1;
			_resInterceptor = -1;
		};
	}, []);

	if (setting.panelLogin)
		return (
			<Router>
				<Routes>
					<Route path="/login" caseSensitive={false} element={<Login />} />
				</Routes>
			</Router>
		);

	return (
		//disable StrictMode that is bug GENERATOR
		// <React.StrictMode>

		// <>
		// 	<HashRouter>
		// 		<Route path="/Common/ComponentProfiles" element={<SettingView />} />
		// 		<Route path="*" element={<Navigate replace to="/register" />} />
		// 	</HashRouter>

		<Router>
			<Routes>
				{/* #/Common/ComponentProfiles */}
				{/* <Route path="/"> */}

				{/* </Route> */}

				<Route path="*" element={<Home2></Home2>} />
			</Routes>
		</Router>
		// </>
		// </React.StrictMode>
	);
};

export default App;
