import * as React from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { useFormik } from "formik";
import { object, string } from "yup";
import {
	TextField,
	Alert,
	InputAdornment,
	InputLabel,
	FormControl,
	IconButton,
	OutlinedInput,
	FormHelperText,
	CircularProgress,
	Container,
} from "@mui/material";
import { useTranslation } from "react-i18next";
import { LoadingButton } from "@mui/lab";
import { SetStateAction, useState } from "react";
import axios from "../../utils/axios-instance";
import axiosOrigin from "axios";
import { VisibilityOff, Visibility, Language } from "@mui/icons-material";

import Logo from "../../images/logo.svg";

import AuthBannerBg from "../../images/auth-banner-bg.jpg";
import AuthBanner from "../../images/auth-banner.png";

import InfoIcon from "@mui/icons-material/Info";
import LanguageSelector from "../../components/LanguageSelector";
import setting from "../../settings";
import { useSnackbar } from "notistack";
import { MuiOtpInput } from "mui-one-time-password-input";
import { setScreenLoading } from "../../utils/index";

const isDemo =
	window.location.host.includes("localhost") ||
	window.location.host.includes("demo.");

const Login = () => {
	// if (!setting.panelLogin && !isDemo) {
	// 	//redirect to .ir
	// 	if (window.location.host.includes("clickmis.net")) {
	// 		setScreenLoading(true);
	// 		window.location.assign("https://clickmis.net/login");
	// 		return <></>;
	// 	}
	// }

	const { t } = useTranslation();
	const navigate = useNavigate();
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState("");
	const [veryfySms, setVeryfySms] = useState({
		username: "",
		password: "",
		enable: false,
	});
	const { enqueueSnackbar } = useSnackbar();

	//otp
	const [otp, setOtp] = useState("");
	const handleChange = (value: SetStateAction<string>) => {
		setOtp(value);
	};

	const mobileValidation = !setting.panelLogin
		? string()
				.required(t("mobileRequiredError") as string)
				.matches(/^(0)?([9][0-9]{9})$/, t("mobileValidationError") as string)
		: string().required(t("mobileRequiredError") as string);

	const loginForm = useFormik({
		initialValues: {
			mobile: "",
			password: "",
		},
		validationSchema: object({
			mobile: mobileValidation,
			password: string()
				.required(t("passwordRequiredError") as string)
				.min(4, t("passwordValidationMinError") as string),

			// .max(4, 'رمز عبور باید ۴ رقم باشد')
			// .matches(/^[0-9]+$/, 'Password requires a number')
		}),
		onSubmit: (values) => {
			setLoading(true);

			if (setting.panelLogin) {
				var bodyFormData = new FormData();

				bodyFormData.append("password", values.password);
				bodyFormData.append("username", values.mobile);
				bodyFormData.append("OTP", otp);

				return (
					axiosOrigin({
						method: "post",
						url: "/Login",
						data: bodyFormData,
						headers: { "Content-Type": "multipart/form-data" },
					})
						.then((response) => {
							if (response.data?.result === "ok") {
								//its ok and user will receive password
								enqueueSnackbar(
									"کاربر " + response.data.user.FullName + " خوش آمدید",
									{
										variant: "success",
									}
								);

								setScreenLoading(true);
								if (
									window.location.hash &&
									window.location.hash.toLocaleLowerCase().startsWith("#ticket")
								) {
									return window.location.assign(
										"/" + window.location.hash.substring(1)
									);
								}

								return window.location.assign("/" + window.location.hash);
							}
							if (response.data?.result == "otp") {
								//its ok and user will receive password
								enqueueSnackbar(response.data.message, {
									variant: "success",
								});

								setVeryfySms({
									username: bodyFormData.get("username") + "",
									password: bodyFormData.get("password") + "",
									enable: true,
								});
							} else {
								setError(
									response.data?.error || (t("resultInvalidError") as string)
								);

								enqueueSnackbar(response.data?.error, {
									variant: "error",
								});

								if (response.data?.redirectTo) {
									alert(
										response.data?.error ||
											"مدت زمان دموی رایگان شما به پایان رسیده است !"
									);

									setScreenLoading(true);

									return window.location.assign(response.data?.redirectTo);
								}
							}
						})
						.catch((error) => {
							if (error._handled) setError(t("loginErrorMessage") as string);
							else setError(t("loginCatchMessage") as string);
						})
						// .finally(() => setLoading(false));
						.finally(() => {
							setLoading(false);
							setOtp("");
							focusOtp();
						})
				);
			}

			return axios
				.post("user/login", {
					username: values.mobile,
					password: values.password,
				})
				.then((response) => {
					if (response.data?.result === "ok") {
						//set token
						window.localStorage.token = response.data?.token;
						window.localStorage.isAdmin = response.data?.isAdmin;

						// //check user status
						// //if had payment go create site
						// //if created site go to site
						// const user = response.data.user;
						// if (user && user.siteName) {
						// 	//user had site
						// 	return window.location.assign(
						// 		`https://${user.siteName}.clickmis.net`
						// 	);
						// }

						// if (user && user.planName && user.paymentStatus === "ok") {
						// 	//if had payment go create site
						// 	return navigate("/payment-result", { state: { user } });
						// }

						navigate(
							values.mobile === "09124140113" ? "/dashboard" : "/choose"
						);
						// navigate(response.data?.isAdmin ? '/dashboard' : '/choose');
					} else {
						setError(t("resultInvalidError") as string);
					}
				})
				.catch((error) => {
					if (error._handled) setError(t("loginErrorMessage") as string);
					else setError(t("loginCatchMessage") as string);
				})
				.finally(() => setLoading(false));
		},
	});

	const [showPassword, setShowPassword] = React.useState(false);

	const handleClickShowPassword = () => setShowPassword((show) => !show);

	const handleMouseDownPassword = (
		event: React.MouseEvent<HTMLButtonElement>
	) => {
		event.preventDefault();
	};

	const validationCheck = () => {
		!loginForm.isValid &&
			enqueueSnackbar(t("formValid"), {
				variant: "error",
			});
	};

	const otpRef = React.useRef(null);

	const focusOtp = () => {
		//@ts-ignore
		otpRef.current?.querySelector("input")?.focus();
	};

	React.useEffect(() => {
		focusOtp();
	}, [veryfySms.enable]);

	return (
		<Container className="flex min-h-screen flex-col items-center lg:items-stretch">
			<div className="hidden justify-end pt-7 lg:flex">
				<LanguageSelector />
			</div>
			<main className="flex grow justify-between">
				<div className="flex max-w-[480px] pt-8 lg:shrink-0 lg:items-center lg:pt-0">
					<div className="w-full">
						<div className="mb-4 flex flex-col items-center gap-1">
							<Link to="/" className="mb-10">
								<img
									src={Logo}
									alt="نرم افزار حسابداری آنلاین و ابری کلیک"
									className="h-[68px] lg:h-[92px]"
								/>
							</Link>
							{/* <h3 className="mb-4 text-xl font-extrabold text-[#212B36] lg:text-2xl">
								{t("loginTitle")}
							</h3> */}
							{/* <p className="ml-1 mb-4 text-lg font-normal text-[#2E3947]">
								{t("loginSubTitle")}
							</p> */}
						</div>

						{!setting.panelLogin && (
							<Alert
								severity="info"
								className="mb-6 w-full"
								color="info"
								sx={{ fontSize: "16px", textAlign: "center" }}
								classes={{ message: "text-[#091A7A]", icon: "text-[#3366FF]" }}
								icon={<InfoIcon sx={{ color: "#3366FF" }} />}
							>
								{t("loginAlert")}
							</Alert>
						)}

						{error && (
							<Alert severity="error" className="mb-6 w-full">
								{error}
							</Alert>
						)}

						<form onSubmit={loginForm.handleSubmit}>
							{veryfySms.enable ? (
								<>
									<div className="relative mb-6">
										<MuiOtpInput
											value={otp}
											style={{ direction: "ltr" }}
											TextFieldsProps={{
												placeholder: "-",
												type: "tel",
											}}
											ref={otpRef}
											onChange={handleChange}
											onComplete={loginForm.submitForm}
										/>

										{loading && (
											<div className="absolute inset-0 flex items-center justify-center rounded-lg bg-gray-500/30 text-white">
												<CircularProgress color="inherit" size={30} />
											</div>
										)}
									</div>
								</>
							) : (
								<>
									<TextField
										autoFocus
										fullWidth
										label={t("mobile")}
										className="mb-5"
										error={
											loginForm.touched.mobile &&
											Boolean(loginForm.errors.mobile)
										}
										helperText={
											loginForm.touched.mobile && loginForm.errors.mobile
										}
										{...loginForm.getFieldProps("mobile")}
									/>

									<FormControl
										fullWidth
										className="mb-5"
										variant="outlined"
										error={
											loginForm.touched.password &&
											Boolean(loginForm.errors.password)
										}
									>
										<InputLabel htmlFor="outlined-adornment-password">
											{t("password")}
										</InputLabel>
										<OutlinedInput
											{...loginForm.getFieldProps("password")}
											id="outlined-adornment-password"
											type={showPassword ? "text" : "password"}
											endAdornment={
												<InputAdornment position="end">
													<IconButton
														aria-label="toggle password visibility"
														onClick={handleClickShowPassword}
														onMouseDown={handleMouseDownPassword}
														edge="end"
													>
														{showPassword ? <VisibilityOff /> : <Visibility />}
													</IconButton>
												</InputAdornment>
											}
											label={t("password")}
										/>

										<FormHelperText id="outlined-weight-helper-text">
											{loginForm.touched.password && loginForm.errors.password}
										</FormHelperText>
									</FormControl>
								</>
							)}

							<LoadingButton
								disableElevation
								fullWidth
								variant="contained"
								size="large"
								type="submit"
								className="bg-blue-700 hover:bg-blue-900"
								loading={loading}
								onClick={validationCheck}
								sx={{
									"&:hover": {
										boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
									},
								}}
							>
								{t("loginSubmit")}
							</LoadingButton>
						</form>

						{isDemo && (
							<div className="mt-6 flex justify-center text-base">
								<p className="ml-1 mr-1 text-[#919EAB]">{t("notRegiser")}</p>

								<Link
									to="https://clickmis.net/register"
									className="font-normal text-[#3366FF] hover:underline"
								>
									{t("doRegiser")}
								</Link>
							</div>
						)}
						{!setting.panelLogin && (
							<>
								<div className="mt-6 flex justify-center text-base">
									<Link
										to="/reset-password"
										className="font-normal text-[#FF4842] hover:underline"
									>
										{t("forgetPassword")}
									</Link>
								</div>
								<div className="mt-6 flex justify-center text-base">
									<p className="ml-1 mr-1 text-[#919EAB]">{t("notRegiser")}</p>

									<Link
										to="/register"
										className="font-normal text-[#3366FF] hover:underline"
									>
										{t("doRegiser")}
									</Link>
								</div>
							</>
						)}
						<div className="flex justify-center lg:hidden">
							<LanguageSelector className="top-4 z-[1] lg:absolute lg:left-4" />
						</div>
					</div>
				</div>

				<div className="relative hidden w-[490px] flex-col items-center justify-center overflow-hidden lg:flex">
					<h1 className="mb-5 text-3xl font-bold text-gray-800">
						{t("loginDetailTitle")}
					</h1>

					<h2 className="mb-14 text-lg text-gray-500">
						{t("loginDetailSubtitle")}
					</h2>

					<img
						src={AuthBanner}
						alt="نرم افزار حسابداری آنلاین و ابری کلیک"
						className="w-full object-cover"
						style={{ maxWidth: "550px" }}
					/>
				</div>
			</main>
		</Container>
	);
};

export default Login;
