import axios from 'axios';
import settings from '../settings';
import Cookies from 'js-cookie';

const axiosInstance = axios.create({
	baseURL: settings.apiUrl,
	headers: {
		'Access-Control-Allow-Origin': '*',
	},
});

export const _userGuid = Cookies.get('User') || Cookies.get('PermanentUser');
const parseGuid = (str: string) =>
  str.substring(0, 8) +
  '-' +
  str.substring(8, 12) +
  '-' +
  str.substring(12, 16) +
  '-' +
  str.substring(16, 20) +
  '-' +
  str.substring(20);
  // export const userGuid = process.env.NODE_ENV === 'production' ? parseGuid(_userGuid || '') : 'c4f77356-8ffe-40f2-b890-f15d076e793d'; //demo
// export const userGuid = process.env.NODE_ENV === 'production' ? parseGuid(_userGuid || '') : '8F72AD3F-7D07-4F81-809A-04B17006D270'; //tehrani
export const userGuid = process.env.NODE_ENV === 'production' ? parseGuid(_userGuid || '') : '5b255cf5-f27e-4e28-b1b2-a2da68102bfc';


export default axiosInstance;
