const JalaliDate = {
	g_days_in_month: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
	j_days_in_month: [31, 31, 31, 31, 31, 31, 30, 30, 30, 30, 30, 29],
};

JalaliDate.jalaliToGregorian = function (j_y, j_m, j_d) {
	j_y = parseInt(j_y);
	j_m = parseInt(j_m);
	j_d = parseInt(j_d);
	var jy = j_y - 979;
	var jm = j_m - 1;
	var jd = j_d - 1;

	var j_day_no =
		365 * jy + parseInt(jy / 33) * 8 + parseInt(((jy % 33) + 3) / 4);
	for (var i = 0; i < jm; ++i) j_day_no += JalaliDate.j_days_in_month[i];

	j_day_no += jd;

	var g_day_no = j_day_no + 79;

	var gy =
		1600 +
		400 *
			parseInt(
				g_day_no / 146097
			); /* 146097 = 365*400 + 400/4 - 400/100 + 400/400 */
	g_day_no = g_day_no % 146097;

	var leap = true;
	if (g_day_no >= 36525) {
		/* 36525 = 365*100 + 100/4 */ g_day_no--;
		gy +=
			100 * parseInt(g_day_no / 36524); /* 36524 = 365*100 + 100/4 - 100/100 */
		g_day_no = g_day_no % 36524;

		if (g_day_no >= 365) g_day_no++;
		else leap = false;
	}

	gy += 4 * parseInt(g_day_no / 1461); /* 1461 = 365*4 + 4/4 */
	g_day_no %= 1461;

	if (g_day_no >= 366) {
		leap = false;

		g_day_no--;
		gy += parseInt(g_day_no / 365);
		g_day_no = g_day_no % 365;
	}

	for (
		var i = 0;
		g_day_no >= JalaliDate.g_days_in_month[i] + (i == 1 && leap);
		i++
	)
		g_day_no -= JalaliDate.g_days_in_month[i] + (i == 1 && leap);
	var gm = i + 1;
	var gd = g_day_no + 1;

	gm = gm < 10 ? "0" + gm : gm;
	gd = gd < 10 ? "0" + gd : gd;

	return [gy, gm, gd];
};

const toEnDigit = function (s: any) {
	return s?.replace(/[\u0660-\u0669\u06f0-\u06f9]/g, function (a: any) {
		return a.charCodeAt(0) & 15;
	});
};
export default {
	toEnDigit,
	toDateString: function (s: any, locale?: string, option?: object) {
		return s instanceof Date
			? s
			: this.toEnDigit(
					new Date(s).toLocaleDateString(
						locale || "fa-IR",
						option || {
							year: "numeric",
							month: "2-digit",
							day: "2-digit",
						}
					)
			  );
	},
	getToday: function () {
		return this.toEnDigit(
			new Date().toLocaleDateString("fa-IR", {
				year: "numeric",
				month: "2-digit",
				day: "2-digit",
			})
		);
	},
	jalaliToGregorian: function (date: string, charJoiner?: string) {
		if (!date) return "";
		const splited = date.split(/\D+/);
		return JalaliDate.jalaliToGregorian(
			splited[0],
			splited[1],
			splited[2]
		).join(charJoiner || "-");
	},
};

var _serverDate = new Date();
var _localDate = new Date();
export const setServerDate = (d) => {
	_serverDate = d;
	_localDate = new Date();
};

export const getNow = (addSecond = 0) => {
	const serverDate = _serverDate || new Date();
	const localDate = _localDate || new Date();
	return new Date(+new Date() + (serverDate - localDate) + addSecond * 1000);
};

export const toDateString = function (
	s: any,
	locale?: string,
	option?: object
) {
	s = s instanceof Date ? s : new Date(s);
	return toEnDigit(
		new Date(s).toLocaleDateString(
			locale || "fa-IR",
			option || {
				year: "numeric",
				month: "2-digit",
				day: "2-digit",
			}
		)
	);
};

export const toDateTimeString = function (
	s: any,
	locale?: string,
	option?: object
) {
	s = s instanceof Date ? s : new Date(s);
	return toEnDigit(
		s.toLocaleTimeString(
			"fa-IR",
			option || {
				hour: "2-digit",
				minute: "2-digit",
				// second: "2-digit",
			}
		) +
			" " +
			s.toLocaleDateString(
				locale || "fa-IR",
				option || {
					year: "numeric",
					month: "2-digit",
					day: "2-digit",
				}
			)
	);
};

export const toTimeString = function (
	seconds: any, //second
	showSecond = false
) {
	return (
		[seconds / 3600, (seconds % 3600) / 60, (seconds % 3600) % 60]
			.filter((r, i) => (showSecond ? 1 : i < 2))
			.map((v) => (v < 10 ? "0" + parseInt(v) : parseInt(v)))
			// .filter((i, j) => i !== "00" || j > 0)
			.join(":")
	);
};

function parseJwt(token) {
	var base64Url = token.split(".")[1];
	var base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
	var jsonPayload = decodeURIComponent(
		window
			.atob(base64)
			.split("")
			.map(function (c) {
				return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
			})
			.join("")
	);

	return JSON.parse(jsonPayload);
}

export const getTokenUser: () => User | null = function () {
	try {
		const token = localStorage.token || "";
		return parseJwt(token);
	} catch (e) {}
	return null;
};
