import React from "react";
import Logo from "../images/logo.png";

const ScreenLoading = () => {
	return (
		<div className="_loadingContainer">
			<img
				src={Logo}
				alt="نرم افزار حسابداری آنلاین و ابری کلیک"
				className="h-16"
			/>
			<div className="_loadingDiv1" />
			<div className="_loadingDiv2" />
		</div>
	);
};

export default ScreenLoading;
